import { createRouter, createWebHistory } from 'vue-router'
import type { RouteRecordRaw } from 'vue-router'
import MainLayout from '../layouts/MainLayout.vue'
import Login from '../views/Login.vue'
import Dashboard from '../views/Dashboard.vue' 
import System from '../views/System.vue'
import UserProxies from '../views/UserProxies.vue'
import { useUserStore } from '../stores/user' 

const routes: RouteRecordRaw[] = [
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: { requiresAuth: false }
  },
  {
    path: '/',
    component: MainLayout,
    meta: { requiresAuth: true },
    children: [
      {
        path: '',
        name: 'Dashboard',
        component: Dashboard,
        meta: { adminOnly: true }
      },
      {
        path: 'system',
        name: 'System',
        component: System,
        meta: { adminOnly: true }
      },
      {
        path: 'my-proxies',
        name: 'UserProxies',
        component: UserProxies,
        meta: { adminOnly: false }
      },
      {
        path: '/integrated',
        name: 'IntegratedManagement',
        component: () => import('../views/IntegratedManagement.vue'),
        meta: {
          title: '',
          requiresAuth: true
        }
      }
    ]
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

// 
router.beforeEach(async (to, from, next) => {
  const userGroupId = localStorage.getItem('userGroupId')
  console.log('userGroupId', userGroupId)
  // 
  if (to.meta.requiresAuth && !userGroupId) {
    next('/login')
    return
  }
  
  // 
  if (to.path === '/' && userGroupId) {
    const userStore = useUserStore()
    const userGroupId = userStore.userGroupId
    console.log('userGroupIda', userGroupId)
    if (userGroupId) {
      try { 
        // ID1
        if (userGroupId == "") {
          // 
          next()
          return
        } else {
          // 
          next('/my-proxies')
          return
        }
      } catch (error) {
        console.error('', error)
      }
    }
  }
  
  // 
  if (to.meta.adminOnly) {
    const userStore = useUserStore()
    const userGroupId = userStore.userGroupId
    
    if (userGroupId) {
      try { 
        // 
        if (userGroupId === '') {
          next()
          return
        } else {
          // 
          next('/my-proxies')
          return
        }
      } catch (error) {
        console.error('', error)
        next('/login')
        return
      }
    } else {
      next('/login')
      return
    }
  }
  
  next()
})

export default router